.book__overlay {
  min-width: 1280px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.book__overlay-back {
  width: 33px;
  position: absolute;
  bottom: 56px;
  right: 73px;
  transition: 0.2s;
  cursor: pointer;
}

.book__overlay-back:hover {
  opacity: 0.7;
}

.book {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

.book__img {
  display: block;
}

.book__bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 70px;
  padding: 0 68px;
  box-sizing:content-box;
}

.book__bottom-arrow {
  width: 26px;
  transition: 0.2s;
  cursor: pointer;
}

.book__bottom-arrow:hover {
  opacity: 0.7;
}

.book__bottom-numbers {
  width: 494px;
  display: flex;
  justify-content: space-between;
}

.book__display_none {
  display: none;
}

.book__display_block {
  display: block;
}
