.house {
  position: relative;
}

.house__background {
  width: 100%;
  display: block;
}

.house__link {
  position: absolute;
  width: 24%;
  height: 25%;
  top: 12.5%;
  left: 39%;
  cursor: pointer;
  z-index: 1;
}

.house__timer {
  position: absolute;
  top: 70%;
  left: 51%;
  transform: translate(-50%, -50%);
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */
  
  letter-spacing: 0.05em;
  
  color: rgba(255, 255, 255, 0.8);
}