.menu {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  visibility: hidden;
  z-index: 2;
}

.menu__profile {
  position: absolute;
  top: 23px;
  left: 70px;
  display: flex;
  align-items: flex-end;
  visibility: visible;
}

.menu__profile-img {
  width: 30px;
  margin-right: 10px;
}

.menu__profile-name {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: white;
}

.menu__exit {
  position: absolute;
  top: 39px;
  right: 70px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  text-align: right;
  color: white;
  cursor: pointer;
  transition: 0.2s;
  visibility: visible;
}

.menu__exit:hover {
  opacity: 0.7;
}

.menu__book {
  position: absolute;
  bottom: 38px;
  left: 70px;
  cursor: pointer;
  transition: 0.2s;
  visibility: visible;
}

.menu__back {
  width: 33px;
  position: fixed;
  bottom: 56px;
  right: 73px;
  transition: 0.2s;
  cursor: pointer;
  visibility: visible;
}

.menu__back:hover {
  opacity: 0.7;
}