.marina {
  position: relative;
}

.marina__background {
  display: block;
  width: 100%;
}

.tree__one-bird {
  position: absolute;
  width: 8%;
  top: 50%;
  left: 5%;
  cursor: pointer;
}

.tree__one-bird-open {
  position: absolute;
  width: 35%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 2;
}

.tree__one-bird-open_open {
  display: block;
}

