.marina {
  position: relative;
}

.marina__background {
  display: block;
  width: 100%;
}

.marina__cart {
  position: absolute;
  width: 9%;
  top: 68%;
  left: 13%;
  cursor: pointer;
}

.marina__cart-open {
  position: absolute;
  width: 35%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 2;
}

.marina__cart-open_open {
  display: block;
}

.marina__abacus {
  position: absolute;
  width: 8%;
  top: 68%;
  left: 3%;
  cursor: pointer;
}

.marina__abacus-open {
  position: absolute;
  width: 35%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 2;
}

.marina__abacus-open_open {
  display: block;
}