.page {
  position: absolute;
  top: 50px;
  left: 68px;
  right: 68px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 138px;
}

.page__column_start {
  padding-top: 72px;
}

.page__title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  margin-bottom: 50px;
}

.page__text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;  
  color: rgba(0, 0, 0, 0.7);
}

.page__input-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: rgba(0, 0, 0, 0.8);
  margin: 30px 0 13px 0;/*106px 0 13px 0*/
}

.page__input {
  width: 100%;
  height: 46px;
  background: #F9F9F9;
  border-radius: 8px;
  border: none;
  padding: 0 16px;
  box-sizing: border-box;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;  
  color: rgba(0, 0, 0, 0.7);
  outline: none;
  margin-bottom: 49px;
}

.page__input_incorrect {
  text-decoration: line-through;
}

.page__input_correct {
  box-shadow: 1px 1px 10px 1px #52BB00;
}

.page__button {
  width: 108px;
  height: 32px;
  background: rgba(62, 25, 10, 0.9);
  border-radius: 8px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.9);
  cursor: pointer;
  transition: 0.2s;
  cursor: pointer;
}

.page__button:hover {
  opacity: 0.7;
}

.page__column_two {
  padding-top: 72px;
}

.page__triangle {
  width: 100px;
}

.a {
  display: flex;
  align-items: center;
}

.b {
  font-size: 40px;
}