.forest {
  position: relative;
}

.forest__background {
  display: block;
  width: 100%;
}

.forest__overlay {
  position: fixed;
  background-color: black;
  opacity: 0.5;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  z-index: 1;
}

.forest__overlay_open {
  display: block;
}

.forest__list {
  position: absolute;
  width: 10%;
  top: 68%;
  left: 3.5%;
  cursor: pointer;
}

.forest__list-open {
  position: absolute;
  width: 35%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 2;
}

.forest__list-open_open {
  display: block;
}

.forest__back {
  width: 33px;
  position: fixed;
  bottom: 56px;
  right: 73px;
  transition: 0.2s;
  cursor: pointer;
}

.forest__back:hover {
  opacity: 0.7;
}