.main__column {
  position: relative;
  --width: 100%;
  width: var(--width);
  min-width: 1280px
}

.main__background {
  width: 100%;
  display: block;
}

.main__tree {
  position: absolute;
  width: calc(var(--width)*0.22);
  top: 0;
  left: 8.5%;
}

/*
  width: calc(var(--width)*0.182);
  top: 0;
  left: 10.9%;
*/

.main__barn {
  position: absolute;
  width: calc(var(--width)*0.17);
  top: 43.5%;
  left: 8.5%;
}

/*
  width: calc(var(--width)*0.148);
  top: 45.58%;
  left: 9.14%;
*/

.main__marina {
  position: absolute;
  width: calc(var(--width)*0.19);
  top: 67.5%;
  left: 41%;
}

/*
  width: calc(var(--width)*0.143);
  top: 69.13%;
  left: 42.49%;
*/

.main__portal {
  position: absolute;
  width: calc(var(--width)*0.19);
  top: 37%;
  left: 71%;
}

/*
  width: calc(var(--width)*0.139);
  top: 39.96%;
  left: 73.51%;
*/

.main__forest {
  position: absolute;
  width: calc(var(--width)*0.337);
  top: 0;
  left: 55.39%;
}

.main__house {
  position: absolute;
  width: calc(var(--width)*0.21);
  top: 27.5%;
  left: 38.5%;
}

.main__link {
  opacity: 0;
  transition: 0.2s;
  cursor: pointer;
  z-index: 1;
}

.main__link:hover {
  opacity: 1;
}





.main__book:hover {
  opacity: 0.7;
}

.main__display_block {
  display: block;
}

.main__display_none {
  display: none;
}